
(function(c){var d=document,a='appendChild',i='styleSheet',s=d.createElement('style');s.type='text/css';d.getElementsByTagName('head')[0][a](s);s[i]?s[i].cssText=c:s[a](d.createTextNode(c));})
('/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n\n.multiTabbedExhibit {\n    width: 100%;\n    margin: 20px 0;\n    padding : 5px;\n}\n\n.multiTabbedExhibit .templates {display:none;}\n\n.multiTabbedExhibit .passage {position:relative; min-height: 100px;}\n.multiTabbedExhibit .passage240 .frame, .multiTabbedExhibit .passage240 .scrollbar {height:240px;}\n.multiTabbedExhibit .passage440 .frame, .multiTabbedExhibit .passage440 .scrollbar  {height:440px;}\n.multiTabbedExhibit .passage540 .frame, .multiTabbedExhibit .passage540 .scrollbar  {height:540px;}\n\n.multiTabbedExhibit .frame { padding: 0; width: 100%;}\n.frame .slidee { margin: 0;}\n\n.multiTabbedExhibit .scrollbar { width: 10px; position:absolute; right:-7px; top:0; }\n.multiTabbedExhibit .scrollbar .handle {\n    width: 100%;\n    height: 80px; /* overriden if dynamicHandle: 1 */\n    cursor:pointer;\n}\n.multiTabbedExhibit .scrollbar .handle .cursor{\n    background: #266D9C;\n    width:7px;\n    height:100%;\n    right:0px;\n    -webkit-border-radius: 5px;\n    -moz-border-radius: 5px;\n    border-radius: 5px;\n}\n.multiTabbedExhibit .passage-paging, .multiTabbedExhibit .passage-scrolling {\n    position:relative;\n}\n\n.multiTabbedExhibit .passage {margin : 10px 0;}\n\n.multiTabbedExhibit .passage-paging .frame {background:#EFEFEF;}\n.multiTabbedExhibit .passage-paging .frame .pages { margin: 0; padding: 10px; list-style: none; border:#CCC;}\n.multiTabbedExhibit .passage-paging .frame .pages .page {margin: 16px 10px; padding: 10px; background:white;}\n.multiTabbedExhibit .passage-paging .frame .pages li.page.shadowed {\n    -moz-box-shadow: 0px 0px 5px 2px #aaa;\n    -webkit-box-shadow: 0px 0px 5px 2px #aaa;\n    -o-box-shadow: 0px 0px 5px 2px #aaa;\n    box-shadow: 0px 0px 5px 2px #aaa;\n    filter:progid:DXImageTransform.Microsoft.Shadow(color=#aaa, Direction=NaN, Strength=5);\n}\n.multiTabbedExhibit .passage-paging .page-header, .multiTabbedExhibit .passage-paging .page-footer {position:relative; min-height:2rem;}\n.multiTabbedExhibit .passage-paging .page-header {margin-bottom: 12px;}\n.multiTabbedExhibit .passage-paging .page-header .page-number {position:absolute;right:0px;font-weight: bold; font-size:1.8rem;}\n.multiTabbedExhibit .passage-paging .page-content {position: relative; padding:0 20px; min-height:100px;}\n\n.multiTabbedExhibit .passage-paging .passage-pager {position:relative; border:solid 0 #CCC; border-top-width: 1px;}\n.multiTabbedExhibit .passage-paging .passage-pager:after {clear:both; content:\"\"; display:table;}\n.multiTabbedExhibit .passage-paging .passage-pager button {width:96px;}\n.multiTabbedExhibit .passage-paging .passage-pager .passage-pager-previous {position:absolute; top:3px; left:3px;}\n.multiTabbedExhibit .passage-paging .passage-pager .passage-pager-next {position:absolute; top:3px; right:3px;}\n.multiTabbedExhibit .passage-paging .passage-pager .passage-pager-counter {text-align:center; padding:5px 6px; font-weight: bold;font-size:1.5rem;}\n.multiTabbedExhibit .passage-paging .passage-pager .passage-pager-counter span {margin:0 3px;}\n.multiTabbedExhibit .passage-paging .disabled {cursor:default !important;}\n\n.multiTabbedExhibit .passage-scrolling, .multiTabbedExhibit .passage-paging {border:solid 1px #CCC;}\n.multiTabbedExhibit .passage-scrolling .passage-content {position: relative; padding:1rem;}\n\n/*tab style*/\n.multiTabbedExhibit ul.passages-tab-navigation {\n    box-sizing: border-box;\n    width:100%;\n    line-height: 20px;\n    padding:0;\n    margin:0;\n    list-style: none;\n    border-bottom:1px solid #DDD;\n}\n.multiTabbedExhibit ul.passages-tab-navigation:after, .multiTabbedExhibit ul.passages-tab-navigation:before{ content:\"\"; display:table;}\n.multiTabbedExhibit ul.passages-tab-navigation:after {clear:both;}\n\n.multiTabbedExhibit ul.passages-tab-navigation li {\n    margin-bottom:-1px;\n    float:left;\n    position: relative;\n}\n.multiTabbedExhibit ul.passages-tab-navigation li a {\n    display: block;\n    padding: 8px 12px;\n    border-width: 1px 1px 0px;\n    border-style: solid;\n    border-color: transparent;\n    -moz-border-top-colors: none;\n    -moz-border-right-colors: none;\n    -moz-border-bottom-colors: none;\n    -moz-border-left-colors: none;\n    border-image: none;\n    color: #1D8ACB;\n    text-decoration: none;\n    border-radius: 4px 4px 0px 0px;\n    text-shadow: 0px 1px 0px white;\n    cursor:pointer;\n    outline: 0px none;\n}\n.multiTabbedExhibit ul.passages-tab-navigation li a:focus {\n    border-color: #DDD;\n    border-style: dashed;\n    color: #0B5F90 !important;\n}\n.multiTabbedExhibit ul.passages-tab-navigation li:not(.passages-tab-active) a:hover {\n    border-color: #DDD;\n    background: #F1F1F1;\n    color: #0B5F90;\n    outline: 0px none;\n    margin-bottom: 1px;\n    padding-bottom: 7px;\n}\n\n.multiTabbedExhibit ul.passages-tab-navigation > li:nth-child(n+2) > a {margin-left: 3px;}\n\n.multiTabbedExhibit ul.passages-tab-navigation li.passages-tab-active a{\n    border-color: #DDD #DDD transparent;\n    color: #666;\n    background: white;\n    cursor:default;\n}\n');
